.poster {
  box-sizing: border-box;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 40vh;
  width: 20vw;
  margin-bottom: 5vh;
  text-align: center;
}

p {
  margin-top: 0vh;
  margin-bottom: 0vh;
}

.poster:hover {
  background-color: lavender;
}

img {
  margin-top: 1vh;
  width: 20vw;
  height: 30vh;
  object-fit: contain;
}
